.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#root {
  height: 100%;
}

.uppercase {
  text-transform: uppercase;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 400px;
  margin: auto;
}

.container > * {
  margin: 5px;
}

.onepage_container {
  height: 100%;
}

.result_container > :last-child {
  margin-bottom: 40px;
}

.logo {
  margin: 43px 0 20px;
}
/*
@media screen and (max-aspect-ratio: 9/17) {
  .logo {
    margin: 43px 0 40px;
  }
}
*/
.camera {
  position: relative;
  margin: 0 32px;
  flex: 0.8;  /* stretch */
  overflow: hidden;  /* required to auto size the img */
  display: flex;
  min-height: 200px;
}

.camera .camera_btn {
  position: absolute;
  top: 57%;
  left: 51%;
  transform: translate(-50%, -50%);
}

.camera button {
  font-weight: 900;
  width: 110px;
  height: 110px;
  border-radius: 50%;
  background: none;
  color: white;
  border: none;
}

.camera button:disabled {
  background: rgb(0 0 0 / 50%);
}

.camera img {
  max-height: 100%;
  /*max-width: 352px;*/
  max-width: 100%;
  object-fit: contain;
  margin: auto;
}
/*
@media screen and (max-aspect-ratio: 9/17) {
  .camera img {
    max-width: 352px;
  }
}
*/
.instructions {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  white-space: pre;
  letter-spacing: 3px;
}

.control_instructions {
  margin-top: 45px;
  height: 130px;
}

.instructions :first-child {
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase ;
  line-height: 30px;
}

.instructions :not(:first-child) {
  margin-bottom: 16px;
  font-size: 12px;
  font-weight: 400;
}

form {
  margin: 0 47px;
}

.form-step {
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;
}

.form-step label {
  margin-bottom: 5px;
  padding-left: 5px;
  color: gray;
  font-size: 12px;
}

.form-step input,select {
  border: 2px solid black;
  border-width: 0 0 2px;
  outline: none;
  height: 23px;
  background: white;
}

.form-step input:not(:focus),select:not(:focus) {
  background: none;
}

.form-step select {
  border: 2px solid black;
  border-width: 0 0 2px;
  outline: none;
}

.cgu {
  font-size: 9px;
  margin-bottom: 40px;
  line-height: 18px;
}

.form-step-cgu {
  flex-direction: row;
  align-items: center;
}

.btn {
  width: 80%;
}

.iconBtn {
  display: flex;
  align-items: center;
  background: transparent;
  border: none;
  text-transform: uppercase;
  font-size: 12px;
  gap: 10px;
}

form .btn {
  margin: 20px 0;
  width: unset;
  padding: 0px;
}

.btn button {
  width: 100%;
  height: 64px;
  background: white;
  border: none;
  font-size: 16px;
  text-transform: uppercase;
}

.btn button:disabled {
  background: lightgray;
}

.error {
  color: red;
  font-size: 12px;
}
