@font-face {
  font-family: 'BrilliantCut';
  src: url(./fonts/BrilliantCut-Regular.ttf) format('truetype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'BrilliantCut';
  src: url(./fonts/BrilliantCut-Medium.ttf) format('truetype');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'BrilliantCut';
  src: url(./fonts/BrilliantCut-Black.ttf) format('truetype');
  font-weight: 900;
  font-style: normal;
}

body {
  margin: 0;
  background: #F2F2F2;
  font-family: 'BrilliantCut', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  font-family: 'BrilliantCut', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

@media (max-aspect-ratio: 1/1) {
  html {
    height: 100%;
  }

  body {
    height: 100%;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
